:root {
  --bg-color: #202124;
}

body {
  background-color: var(--bg-color);
  paddin: 0;
  margin: 0;
  overflow: hidden;
}

header {
  display: flex;
  justify-content: flex-end;
  font-family: Arial, sans-serif;
  font-size: 14px;
  nav {
    padding: 15px;
    .right-btn {
      a {
        vertical-align: middle;
        text-decoration: none;
        color: white;
        margin-right: 15px;
        &:visited {
          color: white;
        }
        &:hover {
          text-decoration: underline;
        }
      }
      svg {
        fill: white;
        width: 24px;
        vertical-align: middle;
        &:hover {
          background-color: grey;
          border-radius: 25px;
          cursor: pointer;
        }
      }
      button {
        margin-left: 15px;
        letter-spacing: .25px;
        vertical-align: middle;
        height: 36px;
        width: 117px;
        background-color: cornflowerblue;
        border: 0;
        border-radius: 4px;
        cursor: pointer;
        &:hover {
          background-color: #7f98c0;
        }
      }
    }
  }
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 250px;
  .logo {
    img {
      width: 250px;
    }
  }
  .text {
    input {
      width: 638px;
      background-color: var(--bg-color);
      border: 1px solid grey;
      height: 36px;
      border-radius: 25px;
      margin-top: 25px;
      &:hover {
        background-color: #525050;
        box-shadow: 1px 1px 1px 1px rgb(40, 39, 39);
      }
      &:focus {
        border: 1px solid grey;
      }
    }
    svg {
      fill: #9aa0a6;
      position: absolute;
      width: 20px;
      transform: translate(65%, 180%);
    }
  }
  .input-btn {
    margin-top: 25px;
    button {
      height: 36px;
      width: 153px;
      text-align: center;
      vertical-align: middle;
      background-color: #4f4e4e;
      border: 0;
      border-radius: 4px;
      margin-right: 10px;

      color: white;
      &:hover {
        cursor: pointer;
        border: 1px solid grey;
        background-color: #626262;
      }
    }
  }
}

footer {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  font-family: Arial, sans-serif;
  font-size: 14px;
  width: 100%;
  border-top: 1px solid #212327;
  height: 47px;
  background-color: #171717;
  color: #9aa0a6;
  .left {
    padding: 20px;
    justify-content: flex-start;
    min-width: 30%;
    order: 2;
    align-self: center;
    a {
      padding: 15px;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
      &:visited {
        color: white;
      }
    }
  }
  .middle {
    align-self: center;
    padding: 20px;
    right: 0;
    justify-content: flex-end;
    min-width: 20%;
    order: 3;
    text-align: center;
    a {
      color: #9aa0a6;
      padding: 15px;
      text-decoration: none;
      text-align: center;
      &:hover {
        text-decoration: underline;
      }
      &:visited {
        color: white;
      }
    }
    img {
      width: 12px;
      height: 14px;
    }
  }
  .right {
    padding: 20px;
    right: 0;
    justify-content: flex-end;
    min-width: 30%;
    order: 4;
    align-self: center;
    a {
      color: #9aa0a6;
      padding: 15px;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
      &:visited {
        color: white;
      }
    }
  }
}